<template>
  <div class="container text-center" style="margin-top: 100px; margin-bottom: 40px">
    <!-- Heading Row -->
    <div class="row align-items-center my-3">
      <div class="col-lg-6">
        <h1 class="font-weight-light">  </h1>
        <div class="form-inline justify-content-center" id="frm" action="#" method="post">
          <div class="form-group md-1">
            <select
              class="form-control"
              name="gugun"
              id="gugun"
              v-model="gugun"
              @change="getdong"
              style="width: 128px"
            >
              <option value="all" selected disabled hidden></option>
              <option v-for="gu in gulist" :value="gu.gugun_name" :key="gu.gugun_name">
                {{ gu.gugun_name }}
              </option>
            </select>
          </div>
          <div class="form-group md-1">
            <select
              class="form-control"
              name="dong"
              id="dong"
              v-model="dongname"
              style="width: 128px"
            >
              <option value="all" selected disabled hidden></option>
              <option v-for="dong in donglist" :key="dong.dong" :value="dong.dong">
                {{ dong.dong }}
              </option>
            </select>
          </div>
          <div class="form-group md-1 m-1">
            <button class="btn btn-primary" id="query" @click="searchDong"></button>
          </div>
        </div>
      </div>
      <!-- /.col-lg-8 -->
      <div class="col-lg-6">
        <h1 class="font-weight-light">  </h1>
        <div action="#" method="post">
          <div class="input-group justify-content-center">
            <div class="form-inline">
              <input
                type="search"
                name="name"
                id="form1"
                class="form-control"
                v-model="name"
                placeholder="  "
              />
              <label class="form-label" for="form1"></label>
              <button class="btn btn-primary ml-1" id="query" @click="searchName"></button>
            </div>
          </div>
        </div>
      </div>
      <!-- /.col-md-4 -->
    </div>
    <!--   -->
    <div class="m-4">
      <h2 class="m-2"> </h2>
      <div class="masthead-content d-flex">
        <div class="col-8" id="map"></div>
        <div class="col-4" id="info" style="border: 2px solid #464f41; padding: 0px; margin: 15px">
          <div class="list-group d-flex overflow-auto" style="height: 500px">
            <div
              v-for="deal in deallist"
              :key="deal.no"
              @click="SEARCHDETAIL(deal.aptName)"
              class="
                list-group-item list-group-item-action
                flex-column
                align-items-start
                active
                mb-1
              "
              id="link1"
              @mouseenter="moveMap(deal)"
            >
              <div class="d-flex w-100 justify-content-between">
                <h5 class="mb-1">{{ deal.aptName }}</h5>
              </div>
              <p class="mb-1" id="priceInfo">{{ deal.dealAmount }} ()</p>
              <p class="mb-1" id="sizeInfo"> : {{ deal.area }} m<sup>2</sup></p>
              <p class="mb-1" id="typeInfo">
                : <span v-if="deal.type == 1"> </span>
              </p>
              <small id="dateInfo"
                >{{ deal.dealYear }} {{ deal.dealMonth }} {{ deal.dealDay }}</small
              >
            </div>
            <div v-if="deallist.length == 0">
              <center>
                <h5 class="m-5">   .</h5>
              </center>
            </div>
          </div>
        </div>
      </div>
      <!-- google map -div -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      gugun: "all",
      dongname: "all",
      name: "",
      gulist: [],
    };
  },
  computed: {
    donglist() {
      return this.$store.state.donglist;
    },
    deallist() {
      return this.$store.state.deallist;
    },
    infolist() {
      return this.$store.state.infolist;
    },
  },
  created() {
    this.$store.dispatch("GETGU").then(() => {
      this.gulist = this.$store.state.gulist;
    });
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=a2cbba81ce221f6d1aa8f9459931938b";
      document.head.appendChild(script);
    }
  },
  watch: {
    infolist() {
      this.printMarker();
    },
  },
  methods: {
    moveMap(deal) {
      for (let index = 0; index < this.infolist.length; index++) {
        if (deal.aptName == this.infolist[index].aptName) {
          var mapContainer = document.getElementById("map"), //   div
            mapOption = {
              center: new kakao.maps.LatLng(this.infolist[index].lat, this.infolist[index].lng), //  
              level: 3, //   
            };
          var map = new kakao.maps.Map(mapContainer, mapOption);
          //   
          var markerPosition = new kakao.maps.LatLng(
            this.infolist[index].lat,
            this.infolist[index].lng
          );
          //  
          var marker = new kakao.maps.Marker({
            position: markerPosition,
          });
          //     
          marker.setMap(map);

          //    
          var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

          for (var i = 0; i < this.infolist.length; i++) {
            if (i == index) continue;
            //     
            var imageSize = new kakao.maps.Size(24, 35);

            //   
            var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

            //  
            marker = new kakao.maps.Marker({
              map: this.map, //   
              position: new kakao.maps.LatLng(this.infolist[i].lat, this.infolist[i].lng), //   
              title: this.infolist[i].aptName, //  ,     
              image: markerImage, //  
            });
            marker.setMap(map);
          }
          break;
        }
      }
    },
    SEARCHDETAIL(name) {
      this.$router.push("/searchDetail/" + name);
    },
    getdong() {
      this.$store.dispatch("GETDONG", this.gugun);
    },
    searchDong() {
      this.$store.dispatch("SEARCHDONG", { dong: this.dongname });
    },
    searchName() {
      this.$store.dispatch("SEARCHNAME", { name: this.name });
    },
    initMap() {
      var mapContainer = document.getElementById("map"), //   div
        mapOption = {
          center: new kakao.maps.LatLng(37.566675761763825, 126.97840912217265), //  
          level: 3, //   
        };
      var map = new kakao.maps.Map(mapContainer, mapOption);
      //   
      var markerPosition = new kakao.maps.LatLng(37.566675761763825, 126.97840912217265);
      //  
      var marker = new kakao.maps.Marker({
        position: markerPosition,
      });
      //     
      marker.setMap(map);
    },
    printMarker() {
      var mapContainer = document.getElementById("map"), //   div
        mapOption = {
          center: new kakao.maps.LatLng(this.infolist[0].lat, this.infolist[0].lng), //  
          level: 3, //   
        };
      var map = new kakao.maps.Map(mapContainer, mapOption);
      //    title  
      var positions = [];
      for (let index = 0; index < this.infolist.length; index++) {
        positions.push({
          title: this.infolist[index].AptName,
          latlng: new kakao.maps.LatLng(this.infolist[index].lat, this.infolist[index].lng),
        });
      }
      //       
      // LatLngBounds   
      var bounds = new kakao.maps.LatLngBounds();

      for (let index = 0; index < this.infolist.length; index++) {
        bounds.extend(new kakao.maps.LatLng(this.infolist[index].lat, this.infolist[index].lng));
      }

      //       
      map.setBounds(bounds);

      //    
      var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

      for (var i = 0; i < positions.length; i++) {
        //     
        var imageSize = new kakao.maps.Size(24, 35);

        //   
        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

        //  
        var marker = new kakao.maps.Marker({
          map: this.map, //   
          position: positions[i].latlng, //   
          title: positions[i].title, //  ,     
          image: markerImage, //  
        });
        marker.setMap(map);
      }
    },
  },
};
</script>

<style scoped>
#link1:hover {
  background-color: #bfcba8;
}
</style>
